body.login {
  background-color: #FAFBFD;
}

body.login form {
  background-color: transparent;
  padding: 0;
  border: none;
  box-shadow: none;
}

body.login.login-split-page .login-split {
  display: none;
}

body.login.login-split-page #login {
  position: relative;
  left: 0;
  right: 0;
  top: 50%;
  padding: 30px;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

@media (min-width: 992px) {
  body.login.login-split-page .login-split {
    width: 50%;
    position: fixed;
    left: 0px;
    height: 100%;
    display: block;
  }

  body.login.login-split-page .login-split .split-overlay {
    width: 100%;
    height: 100%;
    background-color: #000000;
    position: relative;
    z-index: 1;
    opacity: 0;
  }

  body.login.login-split-page .login-split div {
    color: #fff;
  }

  body.login.login-split-page .login-split>div:not(.split-overlay) {
    display: block;
    width: 50%;
    max-width: 300px;
    margin: 0 auto;
    padding: 8% 0 0;
    font-size: 32px;
    line-height: 1.15;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 9;
    margin: auto;
  }

  body.login.login-split-page .login-split>div>span {
    font-size: 16px;
    line-height: 1.4;
    display: block;
    margin-top: 20px;
  }

  body.login.login-split-page #login {
    position: absolute;
    left: 50%;
    right: auto;
    top: 50%;
    padding: 30px 50px;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
  }

  body.login.login-split-page.login-exh #login {
    -webkit-transform: translateY(-35%);
    -ms-transform: translateY(-35%);
    transform: translateY(-35%);
  }

  body.login.login-split-page #login h1 a {
    margin-left: 0;
    text-align: left;
  }
}

.login #backtoblog,
.login #nav {
  padding: 0;
}

.bs-cs-login-title {
  font-size: 26px;
}

.login h1 a {
  height: auto;
}

.login input[type=text],
.login input[type=email],
.login input[type=password] {
  background-color: #fff;
  border: 1px solid #e3e3e3;
  box-shadow: none;
  border-radius: 3px;
  font-size: 16px;
  padding: 10px 15px;
}

.login form .forgetmenot {
  float: none;
}

#login form p.submit {
  position: relative;
  clear: both;
}

.login-simple.wp-core-ui #login .button-primary,
.login-simple.wp-core-ui #login .two-factor-email-resend .button,
.login-simple.wp-core-ui #login .wp-generate-pw,
.login-minimal.wp-core-ui #login .button-primary,
.login-minimal.wp-core-ui #login .two-factor-email-resend .button,
.login-minimal.wp-core-ui #login .wp-generate-pw,
.login-creative.wp-core-ui #login .button-primary,
.login-creative.wp-core-ui #login .two-factor-email-resend .button,
.login-creative.wp-core-ui #login .wp-generate-pw,
.login-modern.wp-core-ui #login .button-primary,
.login-modern.wp-core-ui #login .two-factor-email-resend .button,
.login-modern.wp-core-ui #login .wp-generate-pw {
  box-shadow: none;
  height: auto;
  line-height: 20px;
  padding: 13px;
  float: none;
  width: 100%;
}

.login input[type="submit"] {
  margin: 7px 0 7px;
}


/* Login Layouts */
.login.rg-login.login-simple #login,
.login.rg-login.login-minimal #login,
.login.rg-login.login-creative #login {
  position: relative;
  border-radius: 5px;
  max-width: 350px;
  padding: 26px;
  width: 100%;
  left: 0;
  right: 0;
  top: 50%;
  margin: auto;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.login.rg-login.login-simple #login {
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.8);
}

.login.rg-login.login-simple form,
.login.rg-login.login-minimal form {
  background: none;
  padding: 0;
  overflow: visible;
  border: none;
  box-shadow: none;
}

.login.rg-login.login-minimal form .forgetmenot label {
  font-size: 13px;
}

.login.rg-login.login-minimal form .input,
.login.rg-login.login-minimal input[type=text] {
  background: rgba(255, 255, 255, .2);
  display: block;
  color: #fff;
  font-size: 16px;
  width: 100%;
  border: 0;
  height: 50px;
  padding: 0 15px;
}

.login.rg-login.login-minimal:before {
  content: "";
  display: block;
  background: rgba(0, 0, 0, 0.2);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.login.rg-login.login-minimal label,
.login.rg-login.login-minimal #nav,
.login.rg-login.login-minimal #backtoblog,
.login.rg-login.login-minimal form .forgetmenot label,
.login.rg-login.login-minimal #nav a,
.login.rg-login.login-minimal #backtoblog a,
.login.rg-login.login-minimal .privacy-policy-page-link {
  font-size: 14px;
  color: #d5d5d5;
}

.login.rg-login.login-minimal form p.admin-email__details,
.login.rg-login.login-minimal h1.admin-email__heading,
.login.rg-login.login-minimal #backtoblog a,
.login.rg-login.login-minimal .privacy-policy-page-link a {
  color: #fff !important;
}

.login.rg-login.login-minimal .privacy-policy-page-link {
  text-align: left;
  width: 100%;
  margin: 0;
}

.login.rg-login.login-minimal .button.wp-hide-pw {
  height: 3rem;
}

.login.rg-login.login-creative::after {
  width: 50%;
  top: 0;
  left: 0;
  height: 100%;
  background: rgba(255, 255, 255, .7);
  content: '';
  position: absolute;
}

.login.rg-login.login-creative #login {
  left: 25%;
  z-index: 12;
  margin: 0;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.login.rg-login.login-creative form {
  background: none;
  padding: 0;
  overflow: visible;
  border: none;
  box-shadow: none;
}

.login.rg-login.login-creative form .input,
.login.rg-login.login-creative input[type="text"],
.login.rg-login.login-creative input[type="password"] {
  background: none;
  display: block;
  color: #404040;
  font-size: 16px;
  width: 100%;
  border: 0;
  height: 50px;
  padding: 0 15px;
  border-bottom: 1px solid #a8a5a3;
  box-shadow: none;
}

@media screen and (max-width: 767px) {
  .login.rg-login.login-creative #login {
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    margin: 0 auto;
    left: 0;
  }

  .login.rg-login.login-creative::after {
    width: 100%;
  }
}

.bs-cs-login-logo {
  /* visibility: hidden; */
  width: 100%;
  display: block;
}

.login h1 a.rg-login-title {
  background-image: none;
  text-indent: 0;
  width: auto;
  height: auto;
  color: #007cff;
}

.login.rg-login.login-modern .login-split {
  display: none;
}

.login.rg-login.login-modern #login {
  position: relative;
  left: 0;
  right: 0;
  top: 50%;
  padding: 30px;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.login.rg-login.login-modern form {
  background: none;
  padding: 0;
  overflow: visible;
  border: none;
  box-shadow: none;
}

@media (min-width: 992px) {

  .login.rg-login.login-modern .login-split {
    width: 50%;
    position: fixed;
    left: 0px;
    height: 100%;
    display: block;
    background: rgba(255, 255, 255, .7);
    z-index: 1;
  }

  .login.rg-login.login-modern .login-split .split-overlay {
    width: 100%;
    height: 100%;
    background-color: #000000;
    position: relative;
    z-index: 1;
    opacity: 0;
  }

  .login.rg-login.login-modern .login-split div {
    color: #fff;
  }

  .login.rg-login.login-modern .login-split>div:not(.split-overlay) {
    display: block;
    width: 50%;
    max-width: 300px;
    margin: 0 auto;
    padding: 8% 0 0;
    font-size: 32px;
    line-height: 1.15;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 9;
    margin: auto;
  }

  .login.rg-login.login-modern .login-split>div>span {
    font-size: 16px;
    line-height: 1.4;
    display: block;
    margin-top: 20px;
  }

  .login.rg-login.login-modern #login {
    position: absolute;
    left: 50%;
  }

  .login.rg-login.login-modern.login-exh #login {
    -webkit-transform: translateY(-35%);
    -ms-transform: translateY(-35%);
    transform: translateY(-35%);
  }

  .login.rg-login.login-modern #login h1 a {
    margin-left: 0;
    text-align: left;
  }
}

/* Login Video Background  */
.reign-login-video-background-wrapper video#background-video {
  min-width: 100%;
  min-height: 100vh;
  position: fixed !important;
  right: 0 !important;
  bottom: 0 !important;
  width: 100% !important;
  height: 100% !important;
  z-index: -100 !important;
  object-fit: cover;
}

@media (max-width: 480px) {

  .login.rg-login.login-simple #login,
  .login.rg-login.login-minimal #login,
  .login.rg-login.login-creative #login {
    width: 250px;
  }

}

@media (max-width: 360px) {
  .login.rg-login #login {
    width: 84%;
    padding-left: 8%;
    padding-right: 8%;
  }
}